import React from "react"
import styled from "styled-components"
import Googlemaps from "../../assets/google-maps.svg"
import { Facebook2 } from "@styled-icons/icomoon/Facebook2"
import { Messenger } from "@styled-icons/simple-icons/Messenger"

const IconsWrapper = styled.div`
  flex: 50%;
  @media (max-width: 768px) {
    flex: 100%;
    margin-left: auto;
    margin-left: auto;
  }
`

const StyledGooglemaps = styled.img`
  width: 40px;
  height: 40px;
  margin: 20px;
  display: inline-block;
  cursor: pointer;
  color: #38a954;
`

const StyledFacebook = styled(Facebook2)`
  width: 40px;
  height: 40px;
  color: #0e8ef1;
  margin: 20px;
  display: inline-block;
  cursor: pointer;
`

const StyledMessenger = styled(Messenger)`
  width: 40px;
  height: 40px;
  margin: 20px;
  display: inline-block;
  color: #0e8ef1;
  cursor: pointer;
`

const Icons = () => {
  return (
    <IconsWrapper>
      <a
        href="https://www.google.com/maps/place/Paparzyn+11,+86-212+Paparzyn/@53.3627392,18.6248813,445m/data=!3m2!1e3!4b1!4m5!3m4!1s0x4702d781aef7a46f:0x7252b519bc007acf!8m2!3d53.362736!4d18.62707"
        target="_blank"
      >
        <StyledGooglemaps alt="" src={Googlemaps} />
      </a>
      <a
        href="https://www.facebook.com/Firma-z-Drewna-117128053531341"
        target="_blank"
      >
        <StyledFacebook />
      </a>
      <a href="https://m.me/117128053531341" target="_blank">
        <StyledMessenger />
      </a>
    </IconsWrapper>
  )
}

export default Icons
