import React from "react"
import styled from "styled-components"
import { Carousel } from "react-bootstrap"

const Image = styled.img`
  flex: 65%;
  border-radius: 10px;
  background-color: rgb(52, 54, 50, 0.5);
  height: 300px;
  display: flex;
  justify-content: space-between;
  @media (max-width: 768px) {
    flex: 100%;
  }
`

const Slider = ({ photos }) => {
  return (
    <Carousel
      style={{
        width: "30%",
        // margin: "20px 0",
        marginLeft: "auto",
        marginRight: "40px",
        position: "relative",
        bottom: "70px",
      }}
    >
      {photos.map((img, index) => (
        <Carousel.Item key={index}>
          <Image className="d-block w-100" alt="Slide" src={img} />
        </Carousel.Item>
      ))}
    </Carousel>
  )
}

export default Slider
