import React, { Component } from "react"
import styled from "styled-components"
import Logo from "../components/atoms/Logo"
import Menu from "../components/molecules/Menu"
import logo from "../assets/logo.svg"
import background from "../assets/background.jpg"
import MainContent from "../components/molecules/MainContent"
import Slider from "../components/molecules/Slider"
import { importAllFiles } from "../helpers/functions"
import "./index.css"
import "../assets/css/bootstrap.min.css"

const StyledWrapper = styled.div`
  width: 90%;
  min-height: 500px;
  margin: 30px auto 10px auto;
  /* background-image: url(${background}); */
  /* background-color: #ebbe9b;
  background-image: linear-gradient(315deg, #ebbe9b 0%, #e7a977 74%); */
  /* background-size: cover;
  background-color: rgb(255, 255, 255, 0.5); */
  @media (max-width: 830px) {
    width: 100%;
  }
`

class Home extends Component {
  state = {
    photos: importAllFiles(
      require.context(
        "../assets/transparent_photos",
        false,
        /\.(png|jpe?g|svg)$/
      )
    ),
  }
  render() {
    return (
      <StyledWrapper>
        <div style={{ display: "flex", flexWrap: "wrap" }}>
          <Logo src={logo} alt="Logo" />
          <Menu />
        </div>
        <div style={{ display: "flex", flexWrap: "wrap" }}>
          <MainContent />
          <Slider photos={this.state.photos} />
        </div>
      </StyledWrapper>
    )
  }
}

export default Home
