import React from "react"
import Icons from "../atoms/Icons"
import styled from "styled-components"

const StyledWrapper = styled.div`
  font-family: "Poiret One", cursive;
  font-weight: 600;
  flex: 45%;
  margin-top: 15px;
  max-width: 500px;
  margin: 35px 0 10px 25px;
  text-align: left;
  @media (max-width: 768px) {
    flex: 100%;
  }
`

const MainDescription = styled.div`
  cursor: default;
  color: black;
  font-size: 35px;
  margin-bottom: 15px;
`

const StyledPhoneNumber = styled.div`
  flex: 50%;
  cursor: default;
  font-size: 30px;
  margin-top: 20px;
  @media (max-width: 768px) {
    flex: 100%;
    margin-left: auto;
    margin-left: auto;
  }
`

const MainContent = () => {
  return (
    <StyledWrapper>
      <MainDescription>
        Precyzyjne wycinanie laserowe nawet najbardziej skomplikowanych
        projektów.
      </MainDescription>
      <div style={{ display: "flex", flexWrap: "wrap" }}>
        <StyledPhoneNumber>tel. 722 270 317</StyledPhoneNumber>
        <Icons />
      </div>
    </StyledWrapper>
  )
}

export default MainContent
